<template>
    <section v-if="services.length">
        <div class="buttons">
            <button @click="orderByTitle()">{{orderBy == 'z-a' ? "Å–A" : "A–Å"}}</button>
            <button @click="orderByMTime()" v-if="bus.ita_mode"><fa-icon name="clock-o"/></button>
            <button @click="showSummary = !showSummary">
                <fa-icon name="angle-down" v-if="showSummary"/>
                <fa-icon name="angle-right" v-else/>
            </button>
            <input v-model="filter" type="text" placeholder="søk etter tjenester" >
        </div>
        <template v-if="filteredServices.length">
            <h1>Tjenester:</h1>
            <transition-group name="tklist" tag="ul">
                <li v-for="svc in filteredServices" :key="svc.id" :class="['clearfix', svc.state, svc.flag]">
                    <router-link :to="'/node/' + svc.id">{{svc.title}}</router-link>
                    &#32;<span class="domain-link" v-if="svc.domain"><a :href="svc.link">{{svc.domain}}</a></span>
                    &#32;<span class="weight" v-if="bus.ita_mode && orderBy=='weight' && svc.weight">{{svc.weight}}</span>
                    &#32;<span v-if="orderBy=='mtime'" class="changed"><fa-icon name="clock-o"/>&nbsp;{{svc.changed.substr(0,10)}}</span>
                    <a v-if="svc.link" class="svc-link" :href="svc.link" title="Besøk tjenesten"><fa-icon name="external-link"/></a>
                    <p class="summary" v-if="showSummary && svc.summary">{{svc.summary}}</p>
                </li>
            </transition-group>
        </template>
        <p class="err" v-else>
            Ingen tjenester inneholder "{{filter}}".
        </p>
    </section>
    <section v-else>
        <p class="loading">Loading...</p>
    </section>
</template>

<script>
import FaIcon from '@/components/FaIcon.vue';
import { eventBus } from '@/main';
import tkdata from '@/tkdata';

const prettyDomain = function (url) {
    if (!(url && /^https?:\/\//.test(url)))
        return null;
    const segments = url.split('/');
    if (segments.length > 4 || segments[3]) // path and other stuff
        return null;
    let domain = segments[2]
    if (domain.includes(':')) // port
        return null;
    domain = domain.replace(/^www\./, '');
    return domain.toLowerCase();
};

export default {
    props: {
        showSummary: {type: Boolean, default: false}
    },
    data() {
        return {
            services: [],
            orderBy: "a-z",   // "a-z", "z-a", "mtime", "weight"
            filter: "",
            bus: eventBus,
        }
    },
    created() {
        tkdata().then(data => {
            const lst = [];
            Object.values(data.service).forEach(svc => {
                lst.push({
                    id: svc.id,
                    title: svc.title.trim(),
                    summary: svc.text.summary,
                    link: svc.link.home,
                    domain: prettyDomain(svc.link.home),
                    state: svc.info.service_state,
                    changed: svc.meta.changed,
                    flag: svc.flag,
                    weight: 0,
                });
            });
            this.services = lst;
        });
    },
    computed: {
        filteredServices() {
            const filter = this.filter.trim().toLowerCase()
            let services = this.services
            services.forEach(svc => {
                svc.weight = 0;
                if (filter.length) {
                    const domain = svc.domain || '';
                    const title = svc.title.toLowerCase();
                    const summary = svc.summary.toLowerCase();

                    if (domain.startsWith(filter))
                        svc.weight += 1;
                    if (title.startsWith(filter))
                        svc.weight += 1;

                    if (filter.length > 1) {
                        if (title.includes(filter))
                            svc.weight += 1;
                        if (summary.includes(filter))
                            svc.weight += 0.5;
                    }
                }
            });
            this.sortServices();
            if (!this.bus.ita_mode)
                services = services.filter(svc => svc.state != 'avsluttet');
            if (filter)
                return services.filter(svc => svc.weight > 0);
            return services;
        }
    },
    watch: {
        'filter': function() {
            this.orderBy = 'weight';
        }
    },
    methods: {
        sortServices() {
            if (this.orderBy == 'a-z') {
                this.services.sort((a,b) => a.title.localeCompare(b.title, 'nb'));
            }
            else if (this.orderBy == 'z-a') {
                this.services.sort((a,b) => b.title.localeCompare(a.title, 'nb'));
            }
            else if (this.orderBy == 'mtime') {
                this.services.sort((b,a) => a.changed.localeCompare(b.changed, 'nb'));
            }
            else { // orderBy 'weight'
                this.services.sort((a,b) => b.weight - a.weight || a.title.localeCompare(b.title, 'nb'));
            }
        },
        sortByTime() {
            this.show_changed = true;
            this.services.sort((b,a) => a.changed.localeCompare(b.changed, 'nb'));
        },
        orderByTitle() {
            if (this.orderBy == 'a-z')
                this.orderBy = 'z-a';
            else
                this.orderBy = 'a-z'
        },
        orderByMTime() {
            this.orderBy = 'mtime';
        }
    },
    components: {
        FaIcon,
    }
}
</script>

