import Vue from 'vue'
import App from './App.vue'
import router from './router'

Vue.config.productionTip = false

export const eventBus = new Vue({
  name: 'EventBus',
  data: {
    ita_mode: false
  },
  created() {
    const s = window.localStorage;
    if (s.tk_options && s.tk_options.split(' ').includes('ita_mode')) {
      this.ita_mode = true;
    }
  },
  watch: {
    'ita_mode': function(val) {
      if (val) {
        window.localStorage.tk_options = 'ita_mode';
      }
      else {
        window.localStorage.removeItem('tk_options');
      }
    }
  }
});

new Vue({
  router,
  render: h => h(App),
  created() {
    const redirect = sessionStorage.redirect;
    if (redirect) {
      delete sessionStorage.redirect;
      this.$router.push(redirect);
    }
  }
}).$mount('#app')
